import React, { createContext, useReducer } from "react";

export const Context = createContext();

const initialState = {
  auth: false,
  user: null,
  token: null,
};

const reducer = (state, action) => {
  console.log("[info] updating reduce: ", action.type, action.payload)
  switch (action.type) {
    case "LOGIN":
      return {
        ...state,
        auth: (action.payload.token && action.payload.user) ? true : false,
        user: action.payload.user,
        token: action.payload.token,
      };
    case "LOGOUT":
      return {
        ...state,
        auth: false,
        user: null,
      };
    default:
      return state;
  }
};

const ContextProvider = props => {
  const [state, dispatch] = useReducer(reducer, initialState);
  return (
    <Context.Provider
      value={{ state, dispatch }}>
      {props.children}
    </Context.Provider>
  );
};

export default ContextProvider;